import React from 'react';

import {useSelector, useDispatch} from 'react-redux'
import {getCafeList} from '../slices/CafeSlice';
import {Oval} from 'react-loader-spinner';
import { useInView } from 'react-intersection-observer';

import ListView from '../components/ListView';

import style from '../assets/scss/styled.module.scss'

const CafePage = ({query}) => {
    //   
    const [page, setPage] = React.useState(1);

    //   
    const [ref, inView] = useInView();

    //      
    const {rt, rtmsg, item, loading} = useSelector((state) => state.cafe);

    //      
    const dispatch = useDispatch();

    React.useEffect(()=> {
        setPage(1);
    }, [query]);

    // query     hook   
    React.useEffect(() => {
        if(!loading) {
            dispatch(getCafeList({query: query, page: page}));
        }
    }, [dispatch, page]);

    React.useEffect(() => {
        //     ,   
        if(inView && !loading) {
            setPage(page+1);
        }
    }, [inView]);

    return (
        <div>

            {loading && (
                <Oval
                    color='#ff6600'
                    height={100}
                    width={100}
                    wrapperStyle={{
                        position: 'absolute',
                        left: '50%',
                        top: '50%',
                        marginLeft:'-50px',
                        marginTop: '-50px',
                    }}
                />
            )}

            {/*     ,     */}
            {rt !== 200 ? (
                <div className={style.errmsg}>
                    <h3>{rt} Error</h3>
                    <p>{rtmsg}</p>
                </div>
            ): (
                <ListView documents={item.documents} thumb={true} inview={ref} />
            )}
        </div>
    );
};

export default CafePage;