import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import axios from 'axios';

//    
export const getImageList = createAsyncThunk("IMAGE/GET_LIST", async(payload, {rejectWebVitals}) => {
    let result = null;

    if (payload.query) {
        try {
            const apiUrl = 'https://dapi.kakao.com/v2/search/image';
            result = await axios.get(apiUrl, {
                params: {query: payload.query, page: payload.page, size:20},
                headers: {Authorization: 'KakaoAK ff9e717a57a525ac5201c5c326224eea'},
            });
        } catch (err) {
            result = rejectWebVitals(err.response);
        }
    }


    return result;
});

// Slice  (Action + Reduser )
const imageSlice = createSlice({
    // slice 
    name: 'image',
    //   
    initialState: {
        rt: null,
        rtmsg: null,
        item: [],
        loading: false
    },
    //  action   action
    reducers: {},
    //  action   action
    extraReducers: {
        [getImageList.pending] : (state, {payload}) => {
            return {...state, loading: true}
        },
        [getImageList.fulfilled]: (state, {meta,payload}) => {
            // 1            Ajax  .
            if(meta.arg.page > 1) {
                payload.data.documents = state.item.documents.concat(payload.data.documents);
            }
            return {
                ...state,
                rt: payload.status,
                rtmsg: payload.statusText,
                item: payload.data,
                loading: false
            }
        },
        [getImageList.rejected]: (state, {payload}) => {
            return {
                ...state,
                rt: payload?.status ? payload.statusText : '500',
                rtmsg: payload?.statusText ? payload.statusText : 'Server error',
                item: payload?.data,
                loading: false
            }
        }
    }
})

export default imageSlice.reducer;